<template>
  <d2-container>
    <template slot="header">金字塔</template>
    <div class="inner">
      <ve-funnel :data="chartData" :settings="chartSettings" v-bind="pubSetting"></ve-funnel>
    </div>
    <template slot="footer">
      <d2-link-btn title="更多示例和文档" link="https://v-charts.js.org"/>
    </template>
  </d2-container>
</template>

<script>
import list from '@/views/demo/charts/list/_mixin/list.js'
export default {
  mixins: [
    list
  ],
  data () {
    this.chartSettings = {
      ascending: true
    }
    return {
      chartData: {
        columns: ['状态', '数值'],
        rows: [
          { '状态': '展示', '数值': 900 },
          { '状态': '访问', '数值': 600 },
          { '状态': '点击', '数值': 300 },
          { '状态': '订单', '数值': 100 }
        ]
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.inner {
  position: absolute;
  top: 20px;
  right:  20px;
  bottom: 20px;
  left: 20px;
}
</style>

<vue-filename-injector>
export default function (Component) {
  Component.options.__source = "src/views/demo/charts/list/funnel/demo2/index.vue"
}
</vue-filename-injector>
